@media (max-width: 767px) {
  .catalog {
    &__card {
      flex-basis: calc(50% - 20px);
      max-width: calc(50% - 20px);
      &:not(:nth-of-type(3n)) {
        margin-right: 0;
      }
      &:not(:nth-of-type(2n)) {
        margin-right: 40px;
      }
      &:last-child {
        margin-right: auto;
        margin-left: 0;
      }
    }
  }
}
@media (max-width: 480px) {
  .catalog {
    &__tabs {
      overflow: scroll;
    }
    &__tab {
      & + & {
        margin-left: 50px;
      }
    }
    &__list {
      flex-direction: column;
    }
    &__card {
      flex-basis: 100%;
      max-width: 100%;
      &:not(:nth-of-type(2n)) {
        margin-right: 0;
      }
      &:last-child {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }
  .product-card {
    &__title {
      font-size: 25px;
    }
    &__description {
      font-size: 16px;
    }
    &__price {
      font-size: 35px;
    }
  }
}
